import {
    PageClone,
    PageDelete,
    PageNameEdit,
    PageSeoEdit,
    PageSettings,
    PagesList,
    PageSlugEdit,
    PageStatusEdit,
} from './Page';
import { WebsiteMenuEdit } from './Website';

export const pagesRouting = {
    path: 'pages',
    component: PagesList,
    children: [
        { path: 'menu', component: WebsiteMenuEdit },
        {
            path: 'settings',
            component: PageSettings,
            children: [
                { path: 'status', component: PageStatusEdit },
                { path: 'name', component: PageNameEdit },
                { path: 'slug', component: PageSlugEdit },
                { path: 'seo', component: PageSeoEdit },
                { path: 'clone', component: PageClone },
                { path: 'delete', component: PageDelete },
            ],
        },
    ],
};
