import React from 'react';
import { useTranslation } from 'react-i18next';
import { useHistory } from 'react-router-dom';
import { useAsync } from 'react-use';

import { randomSlug } from '@mssgme/shared';
import { useActions, useNavigationUtils, useWebsitePageBySlugs } from '@mssgme/hooks';
import { ModalPane, Spinner } from '@mssgme/ui';
import { usePageInitialData } from '../../../../hooks';

export function PageClone() {
    const history = useHistory();
    const { t } = useTranslation('landings');
    const { generateUrl } = useNavigationUtils();
    const { websiteId, pageId, page, homePageId } = useWebsitePageBySlugs();
    const { api } = useActions();
    const initialValues = usePageInitialData(page, null, homePageId === pageId);

    const { loading } = useAsync(async () => {
        const slug = initialValues.slug.slice(0, 44) + '-' + randomSlug(5);
        const name = t('Page.Edit.Clone.copyName', { name: initialValues.name.slice(0, 240) });
        const result = await api.pages.clone({
            websiteId,
            pageId,
            payload: {
                enabled: true,
                targetWebsiteId: websiteId,
                slug,
                name,
            },
        });

        history.push(`/pages/-/${result.website}/${result._id}/edit/pages`);
    }, [websiteId, pageId, initialValues]);

    return (
        <ModalPane title={t('Page.Edit.Clone.heading')} onClose={generateUrl('..')}>
            {loading && <Spinner />}
        </ModalPane>
    );
}
