import { getBlockIdsRecursively, ProductsBlockKind } from '@mssgme/shared';
import { createReducer, isMatchId, updateResource } from '../helpers';
import { api } from '../actions';

const getWebsiteState = (state, action) => state.find(isMatchId(action.websiteId));
const getPagesState = (state, action) => getWebsiteState(state, action).pages;
const getPageState = (state, action) => getPagesState(state, action).find(isMatchId(action.pageId));
const getBlocksState = (state, action) => getPageState(state, action).blocks;
const getMenuItemsState = (state, action) => getWebsiteState(state, action).menu.items;

const acceptWebsite = updateResource({
    match: (action) => (action.websiteId && isMatchId(action.websiteId)),
    prepend: true,
    onUpdate: (website) => {
        website.menu.items.unshift({
            enabled: true,
            page: website.homePageId,
            label: '',
        });
    },
});

const acceptPage = updateResource({
    collection: getPagesState,
    match: (action) => (action.pageId && isMatchId(action.pageId)),
    onCreate: (page, state, action) => {
        getMenuItemsState(state, action).push({
            enabled: true,
            page: page._id,
            label: '',
        });
    },
    onDelete: (_, state, action) => {
        const items = getMenuItemsState(state, action);
        const idx = items.findIndex(({ page }) => page === action.pageId);

        if (idx >= 0) {
            items.splice(idx, 1);
        }
    },
});

const acceptBlock = updateResource({
    collection: getBlocksState,
    match: (action) => (action.blockId && isMatchId(action.blockId)),
    onCreate: (_, state, action) => {
        const page = getPageState(state, action);

        if (!page.enabled) {
            page.enabled = true;
        }
    },
});
const acceptBlocks = (state, action) => {
    getPageState(state, action).blocks = action.result;
};
const deleteBlocks = (state, action, ids) => {
    const page = getPageState(state, action);
    const allDeleted = [];

    for (const id of ids) {
        const block = page.blocks.find(isMatchId(id));
        const withNested = getBlockIdsRecursively(page, block);

        allDeleted.push(...withNested);
    }

    page.blocks = page.blocks.filter(({ _id }) => !allDeleted.includes(_id));

    if (!page.blocks.length) {
        page.enabled = false;

        const website = getWebsiteState(state, action);

        if (website.homePageId === page._id) {
            website.enabled = false;
        }
    }
};

const acceptDomain = (state, action) => {
    const website = getWebsiteState(state, action);
    const domain = action.result || { name: '' };

    website.domain = domain;
    website.finalUrl = domain.name ? `http://${domain.name}` : website.absoluteUrl;
};

const clearCollectionInFilters = (state, action) => {
    const collectionId = action.collectionId;

    for (const { pages } of state) {
        for (const page of pages) {
            for (const { kind, collections } of page.blocks) {
                if (kind !== ProductsBlockKind) {
                    continue;
                }

                if (collections?.include?.includes(collectionId)) {
                    collections.include = collections.include.filter((id) => id !== collectionId);
                }
            }
        }
    }
};

export default createReducer([], {
    // Websites
    [api.websites.list.success]: (state, action) => action.result,
    [api.websites.create.success]: acceptWebsite,
    [api.websites.scaffold.success]: acceptWebsite,
    [api.websites.update.success]: acceptWebsite,
    [api.websites.updateHomePage.success]: acceptWebsite,
    [api.websites.retrieve.success]: acceptWebsite,
    [api.websites.crm.settings.update.success]: acceptWebsite,
    [api.websites.delete.success]: acceptWebsite,
    [api.websites.domain.update.success]: acceptDomain,
    [api.websites.domain.delete.success]: acceptDomain,

    // Uploads
    // [api.websites.upload.success]: (state, action) => {
    //     state.find(isMatchId(action.websiteId)).uploads.push(action.result);
    // },
    // [api.websites.uploadMany.success]: (state, action) => {
    //     state.find(isMatchId(action.websiteId)).uploads.push(...action.result.success.map(({ result }) => result));
    // },
    // [api.websites.deleteUpload.success]: (state, action) => {
    //     deleteIn(state.find(isMatchId(action.websiteId)).uploads, isMatchId(action.uploadId));
    // },

    // Pages
    [api.pages.applyTemplate.success]: acceptWebsite,
    [api.pages.create.success]: acceptPage,
    [api.pages.update.success]: acceptPage,
    [api.pages.delete.success]: acceptPage,
    [api.pages.clone.success]: acceptPage,

    // Blocks
    [api.blocks.create.success]: acceptBlock,
    [api.blocks.update.success]: acceptBlock,
    [api.blocks.toggle.success]: acceptBlocks,
    [api.blocks.group.success]: acceptBlocks,
    [api.blocks.clone.success]: acceptBlocks,
    [api.blocks.sort.success]: acceptBlocks,
    [api.blocks.convert.success]: updateResource({
        result: (action) => action.result.website,
    }),
    [api.blocks.delete.success]: (state, action) => deleteBlocks(state, action, [action.blockId]),
    [api.blocks.deleteMany.success]: (state, action) => deleteBlocks(state, action, action.payload.blockId),

    [api.collections.delete.success]: clearCollectionInFilters,
});
