import { useLayoutEffect } from 'react';

window.msa = window.msa || ((...args) => {
    (window.msaTasks || (window.msaTasks = [])).push(args);
});

export const useAnalyticsBridge = (pageId) => {
    useLayoutEffect(() => {
        window.msa('view', pageId);

        const handler = (e) => {
            /**
             * @type {Element}
             */
            let el = e.target;

            while (el) {
                if (el === document.body) {
                    return;
                }

                if (el.getAttribute('data-element') === 'custom-link') {
                    window.msa('click_link', pageId, { category: el.getAttribute('data-id') });

                    break;
                }

                const messenger = el.getAttribute('data-messenger') || el.getAttribute('data-product-messenger');

                if (messenger) {
                    window.msa('click_messenger', pageId, { category: messenger });

                    break;
                }

                el = el.parentNode;
            }
        };

        document.addEventListener('click', handler);

        return () => {
            document.removeEventListener('click', handler);
        };
    }, [pageId]);
};
